import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { Wfh, WfhService } from 'modules/work-from-home/work-from-home.service';
import { STATUS } from 'config/constants/wfh';
import { Access } from 'core/providers/access.service';

@Component({
  selector: 'app-review-wfh',
  templateUrl: './review-wfh.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [WfhService]
})
export class ReviewWfhComponent implements OnInit {

  wfhs: Wfh[] = [];

  reviewed: Array<Boolean>;
  approving: Array<Boolean>;
  rejecting: Array<Boolean>;
  loading: boolean = false;

  approveAllowed: boolean;
  rejectAllowed: boolean;

  constructor(
    private wfhService: WfhService,
    private access: Access
  ) {

    this.reviewed = [];
    this.approving = [];
    this.rejecting = []
  }

  async ngOnInit() {

    this.loadWfhs();

    this.approveAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'review',
      action: 'approve'
    });

    this.rejectAllowed = await this.access.hasAccess({
      module: 'work-from-home',
      entity: 'review',
      action: 'reject'
    });
  }

  private loadWfhs() {

    const self = this;
    const query = new Map<string, any>();
    query.set('wfh_status', STATUS.APPLIED);

    this.loading = true;

    this.wfhService.getReviewWfhs(query).then(wfhs => {

      self.loading = false;
      self.wfhs = wfhs;
    });
  }

  approve(wfh_id) {

    this.approving[wfh_id] = true;
    this.wfhService.approve(wfh_id).then(response => {

      this.reviewed[wfh_id] = true;
      this.approving[wfh_id] = false;
    });
  }

  reject(wfh_id) {

    this.rejecting[wfh_id] = true;
    this.wfhService.reject(wfh_id).then(response => {

      this.reviewed[wfh_id] = true;
      this.rejecting[wfh_id] = false
    });
  }
}
